
.customer-reviews {
  padding-top: 80px;
  padding-bottom: 80px;}
 
.customer-reviews__item {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.customer-reviews__image {
  margin-bottom: 32px;
  @media screen and (min-width: 768px){
    margin-bottom: 20px;
  }
  @media screen and (min-width: 1200px){
    margin-bottom: 16px;
  }
}
.customer-reviews__wrapper{
  margin-bottom: 16px;
  padding: 43px 50px 20px 50px;
  background-color: #fafafa;
  min-width: 280px;
  z-index: -1;
  @media screen and (min-width: 768px) {
    padding: 20px 6px 36px;
  }
  @media screen and (min-width: 1200px) {
    padding: 30px 70px;
  }}

.customer-reviews__feedback {
  position: relative;
  font-weight: 500;
  font-size: 12px;
  line-height: calc(31 / 12);
  text-align: center;
  letter-spacing: 0.04em;
  background-image: linear-gradient(
    0deg,
    #e1e1e1 1.61%,
    transparent 1.61%,
    transparent 50%,
    #e1e1e1 50%,
    #e1e1e1 51.61%,
    transparent 51.61%,
    transparent 100%
  );
  background-size: 62px 62px;

  z-index: 1;
  @media screen and (min-width: 768px) {  
    font-size: 14px;
    line-height: calc(30 / 14);}
  @media screen and (min-width: 1200px) {
      font-size: 16px;
      line-height: calc(30 / 16);
  }
  &::before{
    content: '';
    position: absolute;
    display: block;
    top: -14px;
    left: 10px;
    width: 54px;
    height: 40px;
    background-image: url(../images/desktop/customer-reviews/image4.svg);
    background-repeat: no-repeat;
    background-size: contain;

    z-index: -1;}

    @media screen and (min-width: 480px) {
      .customer-reviews__feedback ::before{ left: -20px;}
     
    }
}
 
  

.customer-reviews__location {
  font-weight: 500;
  font-size: 14px;
  line-height: 186%;
  text-align: center;
  margin-bottom: 30px;
  &::before {
    content: '';
    display: block;
    margin: 0 auto;
    margin-bottom: 18px;
    text-align: center;
    width: 32px;
    height: 4px;

    background-image: url(../images/desktop/customer-reviews/image5.svg);
    @media screen and (min-width: 768px) {
      margin-bottom: 15px;
    }
  }
  @media screen and (min-width: 768px) {
    font-size: 16px;
    margin-bottom: 35px;
  }
  @media screen and (min-width: 1280px) {
    font-size: 20px;
  }
}
.swiper-button-prev:after, .swiper-rtl .swiper-button-next::after {
  color: #E17992;
}
.swiper-button-next:after, .swiper-rtl .swiper-button-prev::after {
  color: #E17992;
}





